.container{
  
  position: fixed;
  z-index: 50;
  top: 100px;
  left: 120px;
  width: 80%;
  height: 80%;
  background-color:white;
  .child{
    width: 90%;
    height: 90%;
    border: 1px solid #eee;
    margin: 20px auto;
    border-radius: 8px;
    svg{
      height: 35px;
      margin-top: 10px;
      margin-left: 10px;
      
    }
    label{
      font-weight:450 ;
      font-size:25px ;

    }
    .title{
        display: flex;
        align-items: center;
    }
  }
  .body{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
    input,textarea{
      width: 95%;
      border-radius: 5px;
      border: 1px solid rgb(161, 153, 153);

    }
  }

}